import React from 'react';
import Comment from './Comment';
import CommentTitle from './CommentTitle';
import ToggleShow from './ToggleShow';

export default class CommentList extends React.Component{
	constructor(props){
		super(props);
		this.state = {
			showComments:false
		};
		this.toggleShow = this.toggleShow.bind(this);
	}
	
	toggleShow(){
		this.setState({
			showComments: !this.state.showComments
		})
	}
	
	render(){
		const data = this.props.messages;
		const commentsCount = data.length;
		let commentList;
		if(commentsCount > 0 && this.state.showComments){
			commentList = <ul className="comments-list">
                   {data.map((item,index) => {
						return <Comment text={item.text} name={item.name} key ={item.id} id={item.id} deleteComment={this.props.deleteComment}/>
					})}
                </ul>
		}
		return (
			 <div className="comments-body">
                <CommentTitle commentsCount={commentsCount}/>
                <ToggleShow showComments={this.state.showComments} toggleShow={this.toggleShow}/>
               	{commentList}
            </div>
		)
	}
}


