var messages=[];
(()=>{
	let messagesArr = localStorage.getItem('message');
	let mess = JSON.parse(messagesArr);
	messages = [...mess];
})();


export function addComment(name,text){
	if(text && name){
		messages.push({
			name:name,
			text:text,
			id:(Math.random().toString(36).slice(2))
		});
		let json = JSON.stringify(messages);
		window.localStorage.setItem('message',json);
	}
}

export function deleteComment(id){
	let indexMessage;
	const message = messages.filter((item,index) =>{
		if(item.id === id){
			indexMessage = index;
			return true;
		}
		return false;
	})
	messages.splice(indexMessage,1);
	let json = JSON.stringify(messages);
	window.localStorage.setItem('message',json);
	return messages;
}
console.log(messages)
export default messages;




