import React from 'react';
import CommentsForm from './CommentsForm';
import CommentList from './CommentList';
import messages,{addComment,deleteComment} from '../messages';


export default class Main extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			name:'',
			text:'',
			data:messages
		}
		this.postCommit = this.postCommit.bind(this);	
		this.handleChangeMain = this.handleChangeMain.bind(this);	
		this.handleDelete = this.handleDelete.bind(this);	
	}
	
	postCommit(event){
		event.preventDefault();
		addComment(this.state.name,this.state.text);
		this.setState({
			text:''
		})
	}
	
	handleDelete(event){
		const message = deleteComment(event.target.id);
		this.setState({
			data: message
		})
	}	
	
	handleChangeMain(event){
		const name = event.target.name;
		this.setState({
			[name]: event.target.value
		})
	}
	
	render(){
		const props = {
			name:this.state.name,
			text:this.state.text,
			postCommit:this.postCommit,
			handleChangeMain:this.handleChangeMain
		}
		return (
			<div className='comments-box'>
				<CommentsForm {...props} />
				<CommentList messages={this.state.data} deleteComment={this.handleDelete}/>
			</div>
		)
	}
}


