import React from 'react';


export default class CommentsForm extends React.Component{
	constructor(props){
		super(props);
		this.handleChange = this.handleChange.bind(this);
		this.ref = React.createRef();
	}
	
	handleChange(event){
		this.props.handleChangeMain(event);
	}
	
	componentDidMount(){
		this.ref.focus();
	}
	
	render(){
		return (
			<form className="comments-form" onSubmit={this.props.postCommit}>
                <label for="name">Name</label>
                <input type="text" id="name" name="name" value={this.props.name} onChange={this.handleChange} ref={input => this.ref = input}/>
                <label for="text">Comment</label>
                <textarea name="text" id="text" value={this.props.text} onChange={this.handleChange}></textarea>
                <input type="submit" value="post commit"/>
            </form>
		)
	}
}


