import React from 'react';

export default class Comment extends React.Component{
	handleDelete(event){
		this.props.deleteComment(event);
	}
	
	render(){
		return (
			<li className="comments-item">
                <h3>
                    {this.props.name}
                    <a href="#" onClick={this.handleDelete.bind(this)} id={this.props.id}>delete</a>
                </h3>
				<p>{this.props.text}</p>
            </li>
		)
	}
}


