import React from 'react';

export default class CommentTitle extends React.Component{
	_getCommentCount(commentsCount){
		if(commentsCount > 1){
			return `${commentsCount} comments`
		}
		else if(commentsCount === 1){
			return `1 comment`
		}
		else return 'No comments';
	}
	render(){
		return (
			<div className="comments-title">{this._getCommentCount(this.props.commentsCount)}</div>
		)
	}
}


